              v-for="(banner,index) in bannerList"
              :key="`banner_${index}`"
            >
              <img :src="banner.img" alt>
            </a>
          </div>
          <div class="banner-control">
            <div class="control-page"></div>
            <div class="control-rl">
              <a href="javascript:;" @click="changeBanner(-1)" class="rl-prev">上一张</a>
              <a href="javascript:;" @click="changeBanner(1)" class="rl-next">下一张</a>
