<script>
// @ is an alias to /src
import AppHeader from "@/components/app-header.vue";
import api from "../utils/api";
export default {
  name: "home",
  data() {
    return {
      bannerList: [],
      showBanner: 0,
      productList: []
    };
  },
  created() {
    api.get("menu?type=carousel").then(result => {
      this.bannerList = result;
    });
    api.get("menu?type=index").then(result => {
      this.productList = result;
    });
  },
  methods: {
    changeBanner(index) {
      let len = this.bannerList.length;
      this.showBanner = (index + len + this.showBanner) % len;
    }
  },
  components: {
    AppHeader
  }
};
</script>
