import api from "../../utils/api"
const state = {
    loggedIn: false,
    user: {}
}
const mutations = {
    login(state, result){
        state.loggedIn = true;
        state.user = result
    }
}
const actions = {
    userAction({commit},form){
        return api.post("/login",form).then(result => {
            commit("login",result);
        })
    }
}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}